import AbstractView from '../framework/view/abstract-view';
import { SortType } from '../const';

function createFilterTemplate(currentSortType) {
  return `<form class="trip-events__trip-sort  trip-sort" action="#" method="get">
  <div class="trip-sort__item  trip-sort__item--day">
    <input ${currentSortType === SortType.DAY ? 'checked' : ''} id="sort-day" class="trip-sort__input  visually-hidden" data-sort-type="${SortType.DAY}" type="radio" name="trip-sort" value="sort-day">    <label class="trip-sort__btn" for="sort-day">Day</label>
  </div>

  <div class="trip-sort__item  trip-sort__item--event">
<input ${currentSortType === SortType.EVENT ? 'checked' : ''} id="sort-event" class="trip-sort__input  visually-hidden" data-sort-type="${SortType.EVENT}" type="radio" name="trip-sort" value="sort-event" disabled="">    <label class="trip-sort__btn" for="sort-event">Event</label>
  </div>

  <div class="trip-sort__item  trip-sort__item--time">
     <input ${currentSortType === SortType.TIME ? 'checked' : ''} id="sort-time" class="trip-sort__input  visually-hidden" data-sort-type="${SortType.TIME}" type="radio" name="trip-sort" value="sort-time">
    <label class="trip-sort__btn" for="sort-time">Time</label>
  </div>

  <div class="trip-sort__item  trip-sort__item--price">
<input ${currentSortType === SortType.PRICE ? 'checked' : ''} id="sort-price" class="trip-sort__input  visually-hidden" data-sort-type="${SortType.PRICE}" type="radio" name="trip-sort" value="sort-price">    <label class="trip-sort__btn" for="sort-price">Price</label>
  </div>

  <div class="trip-sort__item  trip-sort__item--offer">
    <input ${currentSortType === SortType.OFFER ? 'checked' : ''} id="sort-offer" class="trip-sort__input  visually-hidden" data-sort-type="${SortType.OFFER}" type="radio" name="trip-sort" value="sort-offer" disabled="">    <label class="trip-sort__btn" for="sort-offer">Offers</label>
  </div>
</form>`;
}


export default class SortView extends AbstractView {
  #handleSortTypeChange = null;
  #currentSortType = SortType.DAY;

  constructor({currentSortType, onSortTypeChange}) {
    super();
    this.#handleSortTypeChange = onSortTypeChange;
    this.#currentSortType = currentSortType;

    this.element.addEventListener('click', this.#sortTypeChangeHandler);
  }

  get template() {
    return createFilterTemplate(this.#currentSortType);
  }

  #sortTypeChangeHandler = (evt) => {
    if (evt.target.tagName !== 'INPUT') {
      return;
    }
    this.#handleSortTypeChange(evt.target.dataset.sortType);
  };
}
